import './styles.scss'
import Imagem from '../../assets/img/mulher.png'
import { useState } from 'react'
import { NotificationManager } from 'react-notifications'

export default function Banner() {
    const [checkbox, setCheckbox] = useState(false)
    const [user, setUser] = useState({
        name: '',
        email: '',
        phone: ''
    })

    function send(e) {
        if (checkbox) {
            if(!user.name){
                e.preventDefault();
                NotificationManager.warning("Preencha seu nome.")
            }

            if(!user.email){
                e.preventDefault();
                NotificationManager.warning("Preencha seu email.")
            }

            if(!user.phone){
                e.preventDefault();
                NotificationManager.warning("Preencha seu celular.")
            }
        } else {
            e.preventDefault();
            NotificationManager.warning("Aceite receber as comunicações do prefessor.")
        }

    }

    return <header>
        <div className="flag-red"></div>
        <div className="flag-content">
            <div className='content'>
                <p className='ballon'>Avance no espanhol e conquiste novas habilidades!</p>
                <img src={Imagem} alt='Aprenda espanhol hoje mesmo!' />
                <div className='card-form'>
                    <h1>Não espere mais! Comece a aprender espanhol hoje mesmo!</h1>
                    <p>Preencha o formulário abaixo, e receba a avaliação feita por um professor nativo.</p>
                    <form onSubmit={(e) => send(e)} method='GET' action='https://wa.me/+5511971257381' target="_blank">
                        <div className='form-field'>
                            <label className='form-label'>Nome:</label>
                            <input
                                className='form-input'
                                type='text'
                                placeholder='Ex: John'
                                name="name"
                                onChange={(e) => setUser({ ...user, [e.target.name]: e.target.value })}
                            />
                        </div>
                        <div className='form-field'>
                            <label className='form-label'>Email:</label>
                            <input
                                className='form-input'
                                type='email'
                                placeholder='ex: john@due.com'
                                name="email"
                                onChange={(e) => setUser({ ...user, [e.target.name]: e.target.value })}
                            />
                        </div>
                        <div className='form-field'>
                            <label className='form-label'>Celular:</label>
                            <input
                                className='form-input'
                                type='text'
                                placeholder='ex: (11) 9 1111-1111'
                                name="phone"
                                onChange={(e) => setUser({ ...user, [e.target.name]: e.target.value })}
                            />
                        </div>
                        <div className='form-checkbox-field'>
                            <input
                                className='form-checkbox'
                                type='checkbox'
                                checked={checkbox}
                                onChange={() => setCheckbox(!checkbox)}
                            />
                            <label>Eu concordo em receber comunicações.</label>
                        </div>
                        <input type='hidden' name='text' value={`Olá, sou ${user.name}, e gostaria de saber mais sobre as aulas de espanhol. Meu telefone é ${user.phone} e meu email é ${user.email}. Fico no aguardo.`}/>
                        <button className='form-button'>RECEBER AVALIAÇÃO</button>
                    </form>
                </div>
            </div>
        </div>
        <div className="flag-red"></div>
    </header>
}